<template>
  <mavon-editor
    ref="mavonEditor"
    :toolbars="toolbars"
    @imgAdd="$imgAdd"
    @change="change"
    @save="save"
    :boxShadow="false"
    style="z-index:1;border: 1px solid #d9d9d9;height:100vh"
    v-model="markdown"
  />
</template>

<script>
import Vue from "vue";
import mavonEditor from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import { uploadFile } from "@/api/index.js";
Vue.use(mavonEditor);

export default {
  name: "markdown-editor",
  props: {
    value: {
      default: () => ({ markdown: "", html: "" }),
    },
  },
  data() {
    return {
      markdown: this.value.markdown,
      html: this.value.html,
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: false, // 
        htmlcode: false, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true, // 
      },
    };
  },
  watch: {
    value() {
      this.markdown = this.value.markdown;
      this.html = this.value.html;
    },
  },
  methods: {
    async $imgAdd(filename, file) {
      const formdata = new FormData();
      formdata.append("file", file);
      const res = await uploadFile(formdata);
      if (res.code === 1) {
        const me = this.$refs.mavonEditor;
        me.$img2Url(filename, res.data.url);
      } else {
        this.$Message.error("");
      }
    },
    /**
     *  HTML 
     */
    wordCount(render) {
      let n = render.replace(/<\/?.+?\/?>/g, "");
      n = n.replace(/\s+/g, "");
      this.wordLength = n === "" ? 0 : n.length;
    },
    change(value, render) {
      if (value === "" || render === "") {
        return;
      }
      this.html = render;
      this.wordCount(render);
      this.$emit("input", {
        markdown: value,
        html: render,
        contentLength: this.wordLength,
      });
      this.$emit("change");
    },
    save() {
      this.$emit("save");
    },
  },
};
</script>